import React  from 'react'
import Header from '../header'
import TitleBar from './titlebar'
import Footer from '../footer' 
import NavBar from  '../navbar'
import Body from './body'

class App extends React.Component{

    constructor(props){
        super(props)
        //console.log(this.props)
        this.ArticleIndex=this.GetPostContent()
        console.log(this.ArticleIndex)
        
    }

    GetPostId(url){
        const new_url=url.split('=')
        console.log(new_url);
        return parseInt(new_url[new_url.length-1])      
    }

    GetPostContent(){
        const PostId=this.GetPostId(this.props.location.search)
        console.log(PostId)
        return PostId
    }
   
    render(){
        return (
       <div>
           <NavBar/>
           <Header Router={this.props} />
           <TitleBar/>
           <Body ArticleIndex={this.ArticleIndex} />
           <Footer/>
       </div>
        )
    } 
}

export default App