import React  from 'react'
import PostSchema from '../../data/postInfoSchema'
import axios from 'axios'
import api from '../../config/api'
class App extends React.Component{

    constructor(props){
        super(props)
        this.state={
            postInfo:[PostSchema],
        }
    } 

    componentDidMount(){
        this.fetcher()
    }

    fetcher(){
        let url=api.hostname+api.routes.getAllPost
        axios.get(url).then((reponse)=>{
            console.log('cool')
            const postInfo=JSON.parse(reponse.data).reverse()
            this.setState({postInfo})
            console.log(JSON.parse(reponse.data));
            // this.forceUpdate()
            
        }).catch((error)=>{
            console.log('une erruer xai produit');
        })
    }

    FackUrlNavigaton(e,url){
        e.preventDefault()
        this.props.Router.history.push(url)
       }
    
   
    render(){
        return (
       <>
                    <aside class="col-md-3 sidebar97239">
                    <div class="status-part3821">
                        <h4>stats</h4> <a href="#"><i class="fa fa-question-circle" aria-hidden="true"> Question({this.state.postInfo.length})</i></a> <i class="fa fa-comment" aria-hidden="true"> Answers(50)</i> </div>
                    <div class="recent-post3290">
                        <h4>Recent Post</h4>
                        {this.state.postInfo.map((info)=>{
                            const postIndex=this.state.postInfo.indexOf(info)
                            const max=3
                            const maxContent=30
                            if (info.content.length > maxContent){
                                info.content=info.content.slice(0,maxContent)+' ...'
                            }
                            if (postIndex>max-1 ) return <></>
                            return (<>
                                <div class="post-details021"> <a onClick={(e)=>{this.FackUrlNavigaton(e,'details?postid='+info.postid)}} href='#'><h5>{info.title}</h5></a>
                                <p>{info.content}</p> <small style={{color: '#848991'}}>{info.lastupdate}</small> </div>
                                <hr/>
                        </>
                            )
                        })}
                    </div>
                </aside>
       </>
        )
    } 
}

export default App