import React  from 'react'
import Content from './content'
import Acide from './acide'
import axios from 'axios'
import Pagination from './pagination'
import PostSchema from '../../data/postInfoSchema'
import api from '../../config/api'

class App extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
        this.state={
            PaginationData:1,
            postInfo:
            [PostSchema],
        }
    } 

    componentDidMount(){
        this.fetcher()
    }

    fetcher(){
        let url=api.hostname+api.routes.getAllPost
        axios.get(url).then((reponse)=>{
            console.log('cool')
            const postInfo=JSON.parse(reponse.data).reverse()
            this.setState({postInfo})
            console.log(JSON.parse(reponse.data));
            // this.forceUpdate()
            
        }).catch((error)=>{
            console.log('une erruer xai produit');
        })
    }

    updatePostList(data){
        console.log(data)
        this.setState({PaginationData:data})
    }
   
    render(){
        return (
       <>
          <section class="main-content920"/>
        <div class="container">
            <div class="row">
                <div class="col-md-9">
                    <div className="m-2">
                    <Pagination  
                        onUpdatePostList={(data)=>{this.updatePostList(data)}}
                        PostInfo={this.state.postInfo} 
                    />
                    </div>
                
                      {this.state.postInfo.map((info)=>{
                          const max =2
                          console.log(info);
                          const defaultImg='images.png'
                          const maxContent=60
                          console.log(JSON.parse(info.comments).length);
                          
                          let PaginationData=this.state.PaginationData-1
                          let test=(data)=>{
                            return (data-1)*3+1
                          }
                          let pageMin=test(PaginationData+1)-1
                          let pageMax=pageMin+max
                          
                          let postIndex=this.state.postInfo.indexOf(info)
                          if( postIndex>=pageMin && postIndex<=pageMax   ){
                                console.log('ca vient de se passer')
                            if (info.content.length > maxContent){
                                info.content=info.content.slice(0,maxContent)+' ...'
                            }
                            return (<Content
                                        PostTitle={info.title}
                                        PostContent={info.content}
                                        PostLink={info.postid}
                                        PostDate={info.lastupdate}
                                        PostImg={defaultImg}
                                        PostAnswers={JSON.parse(info.comments).length}
                                        PostInfo={this.state.postInfo}
                                        Router={this.props.Router}
                                    />)
                          }
                          else{
                              return ''
                          }  
                      }) }
                      </div>
                      <Acide PostInfo={this.state.postInfo} Router={this.props.Router} />
                      </div>
                      </div>
                      <section/>      

       </>
        )
    } 
}

export default App