
import React  from 'react'
import axios from 'axios'
import api from '../../../../config/api'


class App extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
        this.state={
            InputState:{
                name:'',
                email:'',
                website:'',
                post:''
            }
        }
    }

    updateInput(e){
        let InputState = this.state.InputState
        const name = e.target.name
        const data =e.nativeEvent.data

        if(data===null){
            let Newdata =''
            for (let index = 0; index < InputState[name].length-1; index++) {
                const element = InputState[name][index];
                Newdata+=element
            }
            InputState[name]=Newdata
        }
        else{
            InputState[name]+=data
        }
        
        this.setState({InputState})
        // console.log(e);
        //console.log(this.state.InputState);
    }

    formu(e){
        let InputState=this.state.InputState
        let url=api.hostname+'api/post/details/adcomment/id/'+this.props.ArticleIndex+'/name/'+InputState.name+'/email/'+InputState.email+'/website/'+InputState.website+'/post/'+InputState.post+''
        e.preventDefault()
        axios.get(url).then((reponse)=>{
            console.log('formulaire envoyer');
            InputState={
                name:'',
                email:'',
                website:'',
                post:''
            }
            // this.setState({InputState})
        }).catch((error)=>{
            console.log('une erruer xai produit');
        })
        console.log(e);
        this.props.onUpdateInfo()
    }
   
    render(){
        return (
       <div>
<div class="col-8 p-3 ">
<h3>Leave A Reply</h3>
<hr/>
<form class="column" onSubmit={(e)=>{this.formu(e)}} >
    <div class="col-md-12">
        <div class="replay-input-name42389">
            <p>Name*</p>
            <input type="text" name="name" value={this.state.InputState.name} onChange={(e)=>{this.updateInput(e)}} class="name-box24894 input238-design" placeholder="Name"/> </div>
    </div>
    <div class="col-md-12">
        <div class="replay-email-input89298 ">
            <p>E-mail*</p>
            <input type="text" name='email' value={this.state.InputState.email} onChange={(e)=>{this.updateInput(e)}} class="name-box24894 input238-design" placeholder="E-mail"/> </div>
    </div>
    <div class="col-md-12">
        <div class="replay-input-websit128923">
            <p>Website*</p>
            <input type="text" name='website' value={this.state.InputState.website} onChange={(e)=>{this.updateInput(e)}} class="name-box24894 input238-design" placeholder="Website"/> </div>
    </div>
    <div class="post9320-box">
        <input type="text" name='post' value={this.state.InputState.post} onChange={(e)=>{this.updateInput(e)}} class="comment-input219882" placeholder="Enter Your Post"/> </div>
    <button type="submit" class="pos393-submit">Post Your Answer</button>
</form>
</div>
   
       </div>
        )
    } 
}

export default App