import React  from 'react'
import axios from 'axios'
import api from '../../../../config/api'

class App extends React.Component{

    constructor(props){
        super(props)
        //console.log(this.props)
        this.ArticleIndex=this.props.ArticleIndex
        this.state={
            fetchdata:this.props.Data,
            curdata:0
        }
    } 

    componentDidMount(){
        this.fetcher()
        console.log(this.props.Specialdata);
        
    }

    componentDidUpdate(){
        if(this.props.Specialdata<=this.state.curdata ) return 0
        setTimeout(() => {
            this.fetcher()  
        }, 1000);
        this.setState({curdata:this.props.Specialdata})
        console.log('mis ajour effectuer');
    }

    fetcher(){
        const ArticleIndex=this.ArticleIndex
        axios.get(api.hostname+api.routes.getPostInfo+ArticleIndex).then((rep)=>{  
            let result=JSON.parse(rep.data)
            result.autor=JSON.parse(result.autor)
            result.comments=JSON.parse(result.comments)
            //console.log(result) 
            this.setState({fetchdata:result})
            
        })
    }
   
    render(){
        const data= this.state.fetchdata
        let comment = data.comments 
        const ImgDirectories='image/'
        // console.log(comment.lenght);
        // if (comment[0].msg.lenght<10) return <div></div>
        return (
       <div>
           <p hidden>{this.props.Specialdata}</p>
          <div class="comment-list12993">
                        <div class="container">
                            <div class="row">
                                <div class="comments-container">
                                    <ul id="comments-list" class="comments-list">
                                        {comment.map((info)=>{
                                            return( 
                                            <li>
                                                <div class="comment-main-level">
                                                <div class="comment-avatar"><img src={ImgDirectories+info.image} alt=""/></div>
                                                <div class="comment-box">
                                                    <div class="comment-head">
                                                        <h6 class="comment-name"><a href="/">{info.name}</a></h6> <span><i class="fa fa-clock-o" aria-hidden="true"> {info.date}</i></span> <i class="fa fa-reply"></i> <i class="fa fa-heart"></i> </div>
                                                    <div class="comment-content">{info.msg}</div>
                                                </div>
                                                </div>
                                            </li>)
                                        })}                                       
                                    </ul>
                            </div>
                            </div>
                        </div>
                    </div>
       </div>
        )
    } 
}

export default App