import React  from 'react'
import axios from 'axios'
import api from '../../../../config/api'

class App extends React.Component{

    constructor(props){
        super(props)
        this.ArticleIndex=this.props.ArticleIndex
        this.state={
            fetchdata:this.props.Data
        } 
    } 

    componentDidMount(){
        this.fetcher()
    }

    fetcher(){
        const ArticleIndex=this.ArticleIndex
        const url=api.hostname+api.routes.getPostInfo+(ArticleIndex)
        console.log(url);
        axios.get(url).then((rep)=>{  
            let result=JSON.parse(rep.data)
            result.autor=JSON.parse(result.autor)
            this.setState({fetchdata:result})
        })
    }
   
    render(){
        const data=this.state.fetchdata
        const countComment=data.comments.length
        return (
       <div>
                    <div class="post-details">
            <div class="details-header923">
                <div class="row">
                    <div class="col-md-8">
                        <div class="post-title-left129">
                            <h3>{data.title}</h3> </div>
                    </div>
                    <div class="col-md-4">
                        <div class="post-que-rep-rihght320"> <a href="#"><i class="fa fa-question-circle" aria-hidden="true"></i> Question</a> <a href="#" class="r-clor10">Report</a> </div>
                    </div>
                </div>
            </div>
            <div class="post-details-info1982">
                <p>{data.content}</p>
                <hr/>
                <div class="post-footer29032">
                    <div class="l-side2023"> <i class="fa fa-check check2" aria-hidden="true"> solved</i> <a href="#"><i class="fa fa-star star2" aria-hidden="true">  5</i></a> <i class="fa fa-folder folder2" aria-hidden="true"> wordpress</i> <i class="fa fa-clock-o clock2" aria-hidden="true"> 4 min ago</i> <a href="#"><i class="fa fa-commenting commenting2" aria-hidden="true"> {countComment} answer</i></a> <i class="fa fa-user user2" aria-hidden="true"> 70 views</i> </div>
                    <div class="l-rightside39">
                        <button type="button" class="tolltip-button thumbs-up2" data-toggle="tooltip" data-placement="bottom" title="Like"><i class="fa fa-thumbs-o-up " aria-hidden="true"></i></button>
                        <button type="button" class="tolltip-button  thumbs-down2" data-toggle="tooltip" data-placement="bottom" title="Dislike"><i class="fa fa-thumbs-o-down" aria-hidden="true"></i></button> <span class="single-question-vote-result">+22</span> </div>
                </div>
            </div>
        </div>
       </div>
        )
    } 
}

export default App