import React  from 'react'

class App extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
    } 

    componentDidMount(){
        // console.log(this.props.Router.history.push('/fuckyou'))
    }

   FackUrlNavigaton(e,url){
    e.preventDefault()
    this.props.Router.history.push(url)
   }

    render(){
        if(this.props.PostInfo.lenght<1){
            return(
            <div>
                <p>chargement en cours ...</p>
            </div>)
        }
        else{
            return (
                <>    
              <section id="content1">
             <div class="question-type2033">
                 <div class="row">
                     <div class="col-md-1">
                         <div class="left-user12923 left-user12923-repeat">
                             <a href="#"><img src={'image/'+this.props.PostImg} alt="image"/> </a> <a href="#"><i class="fa fa-check" aria-hidden="true"></i></a> </div>
                     </div>
                     <div class="col-md-9">
                         <div class="right-description893">
                             <div id="que-hedder2983">
                                 <h3><a  onClick={(e)=>{this.FackUrlNavigaton(e,'details?postid='+this.props.PostLink)}} href='#' target="">{this.props.PostTitle}</a></h3> </div>
                             <div class="ques-details10018">
                                 <p>{this.props.PostContent}</p>
                             </div>
                             <hr/>
                             <div class="ques-icon-info3293">  <a href="#"><i class="fa fa-clock-o" aria-hidden="true"> {this.props.PostDate}</i></a> <a href="#"><i class="fa fa-question-circle-o" aria-hidden="true"> Question</i></a> <a href="#"><i class="fa fa-bug" aria-hidden="true"> Report</i></a> </div>
                         </div>
                     </div>
                     <div class="col-md-2">
                         <div class="ques-type302">
                             <a href="#" onClick={(e)=>{this.FackUrlNavigaton(e,'details?postid='+this.props.PostLink)}}>
                                 <button type="button" class="q-type238"><i class="fa fa-comment" aria-hidden="true"> {this.props.PostAnswers} answer</i></button>
                             </a>
                             <a href="#">
                                 <button type="button" class="q-type23 button-ques2973"> <i class="fa fa-user-circle-o" aria-hidden="true"> 70 view</i> </button>
                             </a>
                         </div>
                     </div>
                 </div>
             </div>
         </section>
                </>
                 )
        }
        
    } 
}

export default App