import React  from 'react'
import axios from 'axios'
import api from '../../../config/api'

class App extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
        this.state={
            InputState:{
                name:'',
                email:'',
                title:'',
                post:'',
            }
        }
        
    }
    
    urlPush(url){
        setTimeout(() => {
            this.props.Router.history.push(url)
        }, 1000);
        
       }

    updateInput(e){
        let InputState = this.state.InputState
        const name = e.target.name
        const data =e.nativeEvent.data

        if(data===null){
            let Newdata =''
            for (let index = 0; index < InputState[name].length-1; index++) {
                const element = InputState[name][index];
                Newdata+=element
            }
            InputState[name]=Newdata
        }
        else{
            InputState[name]+=data
        }
        
        this.setState({InputState})
        // console.log(e);
        //console.log(this.state.InputState);
    }

    formu(e){
        let InputState=this.state.InputState
        let url=api.hostname+'api/post/ask/title/'+InputState.title+'/name/'+InputState.name+'/email/'+InputState.email+'/post/'+InputState.post+''
        e.preventDefault()
        axios.get(url).then((reponse)=>{
            console.log('question poser !!! ');
            this.urlPush('/')
            
        }).catch((error)=>{
            console.log('une erruer xai produit');
        })
        // console.log(e);
        // this.props.onUpdateInfo()
    }
   
    render(){
        return (
       <>
<div class="col-md-9">
    <div class="ask-question-input-part032">
            <h4>Ask Question</h4>
        <hr/>
        <form onSubmit={(e)=>{this.formu(e)}}>
        <div class="username-part940">
            <span class="form-description43">User name* </span><input type="text" value={this.state.InputState.name} onChange={(e)=>{this.updateInput(e)}} name="name" class="username029" placeholder="Enter your Name"/>  
 </div>
 <div class="email-part320">
     <span class="form-description442">E-Mail* </span><input type="text" value={this.state.InputState.email}  onChange={(e)=>{this.updateInput(e)}} name="email" class="email30" placeholder="Enter Your Email"/>  
                        </div>
                        <div class="question-title39">
                            <span class="form-description433">Question-Title* </span><input value={this.state.InputState.title} onChange={(e)=>{this.updateInput(e)}} type="text" name="title" class="question-ttile32" placeholder="Write Your Question Title"/>
                        </div>
                        
       <div class="details2-239">
        <div class="col-md-12 nopadding">
            <textarea id="txtEditor" onChange={(e)=>{this.updateInput(e)}} name='post' value={this.state.InputState.post} cols="30" rows="10" className='col-12 border-1 border-info ' Mystyle="display: none;" placeholder='entrer votre question ici'></textarea>
        </div>
                        </div>	
                        <div class="publish-button2389">
                    <button type="submit" class="publis1291">Publish your Question</button>
                </div>     
</form>
                 
               
                </div>
             
              
                </div>                
       </>
        )
    } 
}

export default App