import React  from 'react'
import Question from './myquestion'
import Acide from '../acide'

class App extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
    } 
   
    render(){
        return (
       <div>
          <section class=""/>
        <div class="col-12">
            <div class="row">
                <div class="col-md-9">
                    <Question ArticleIndex={this.props.ArticleIndex} />
                </div>
                <Acide/>
                </div>
                </div>
            <section/>   
       </div>
        )
    } 
}

export default App