import React  from 'react'

class App extends React.Component{

    constructor(props){
        super(props)
        this.btn={
            on:'btn-primary',
            off:'btn-secondary',
        }
        this.state = {
            buttonData:[{
                class:this.btn.on
            }]
        }
        
    } 

    componentDidMount(){
        
    }

    componentDidUpdate(){
        // this.calculePage()
    }

    changePage(e){
        const data = e.target.innerText
        this.props.onUpdatePostList(data)
        let buttonData = this.state.buttonData
        buttonData.forEach(element => {
            element.class=this.btn.off
        });
        buttonData[parseInt(data)-1].class=this.btn.on
        this.setState({buttonData})
    }
   
    calculePage(){
        const numberPost=this.props.PostInfo.length
        console.log(numberPost);

    }

    render(){
        return (
<div class="btn-toolbar " role="toolbar" aria-label="Toolbar with button groups">
  <div class="btn-group mr-2" role="group" aria-label="First group">
      {this.props.PostInfo.map((info)=>{
          const index = this.props.PostInfo.indexOf(info)
          const max = 2
          if(index > max) return '' 

          let buttonData = this.state.buttonData.slice()
          let button={
              class:this.btn.off
          }
          buttonData.push(button)
          this.state.buttonData=buttonData
          return <button type="button" onClick={(e)=>{this.changePage(e)}} className={'btn '+this.state.buttonData[index].class} >{index+1}</button>
      })}
  </div>
</div>
        )
    } 
}

export default App