import React  from 'react'
import Qcontent from './Question/qcontent'
import Comment from './Question/comment'
import Formulaire from './Question/fromulaire'
import '../../../library/bootstrap/dist/css/bootstrap.min.css'
import Data from '../../../data/questionModel'
import axios from 'axios'
import api from '../../../config/api'

class QuestionModel extends React.Component{

    constructor(props){
        super(props)
        this.data=Data
        this.state={
            Specialdata:0
        }
        //this.data=this.fetcher()
    }

    fetcher(){
        const result=axios.get(api.hostname+'api/post/details/1').then((rep)=>{
            
            let result=JSON.parse(rep.data)
            console.log(result)
            result.autor=JSON.parse(result.autor)
            //this.data=result
            this.data=result
            //console.log(JSON.parse(result.autor)); 
        })
        
    }

    updateInfo(){
        console.log('updateInfo');
        let Specialdata=this.state.Specialdata
        Specialdata++
        this.setState({Specialdata})
    }
   
    render(){
        return (
<section class="">
    <div class="">
        <div class="row">
            <div class="col-md-12">
                <Qcontent Data={this.data} ArticleIndex={this.props.ArticleIndex} />
                <Comment Specialdata={this.state.Specialdata} Data={this.data} ArticleIndex={this.props.ArticleIndex} />
                <Formulaire onUpdateInfo={(e)=>{this.updateInfo(e)}} Data={this.data} ArticleIndex={this.props.ArticleIndex} />
            </div>
        </div>
    </div>
</section>
        )
    } 
}

export default QuestionModel