import React  from 'react'
import Formulaire from './new'
import Acide from '../acide'
import Header from '../header'
import TitleBar from '../details/titlebar'
import Footer from '../footer' 
import NavBar from  '../navbar'



class App extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
    } 
   
    render(){
        return (
       <div>
           <NavBar/>
           <Header Router={this.props}/>
           <TitleBar/>
           <Body Router={this.props}/>
           <Footer/>
       </div>
        )
    } 
}


class Body extends React.Component{

    constructor(props){
        super(props)
        // console.log(this.props)
    } 
   
    render(){
        return (
       <div>
          <section class=""/>
        <div class="col-12">
            <div class="row">
                <div class="col-md-9">
                    <Formulaire Router={this.props.Router}/>
                </div>
                <Acide/>
                </div>
                </div>
            <section/>   
       </div>
        )
    } 
}

export default App